import './card.styles.css';

const Card = ({ monster: { id, name, email } }) => (
    <div className="card-container">
        <img
            src={`https://robohash.org/${id}.png?set=set2&size=360x360`}
            height="200px"
            width="200px"
            alt={`Mr. ${name}`}
        />
        <h2>{name}</h2>
        <h3>{email}</h3>
    </div>
);

export default Card;
